import React from 'react';
import { useTranslation } from 'react-i18next';

const LotteryOverview = ({
  prizeAmount,
  ticketCount,
  isOpenLottery,
  stopTime,
  isLoading,
  account,
  buyTicket,
  buyMessage,
  buyMessageType,
  getPrizeAmount,
  getTicketCount,
  getIsOpenLottery,
  refreshAllContractData,
  dataFetchFailed,
  formatTimeRemaining
}) => {
  const { t } = useTranslation();

  return (
    <section className="lottery-overview">
      <div className="prize-card">
        <div className="prize-card-header">
          <h2>{t("main.getPrizeAmount")}</h2>
          <div className="prize-card-decoration">
            <svg className="decoration-icon" viewBox="0 0 24 24">
              <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1.41 16.09V20h-2.67v-1.93c-1.71-.36-3.16-1.46-3.27-3.4h1.96c.1 1.05.82 1.87 2.65 1.87 1.96 0 2.4-.98 2.4-1.59 0-.83-.44-1.61-2.67-2.14-2.48-.6-4.18-1.62-4.18-3.67 0-1.72 1.39-2.84 3.11-3.21V4h2.67v1.95c1.86.45 2.79 1.86 2.85 3.39H14.3c-.05-1.11-.64-1.87-2.22-1.87-1.5 0-2.4.68-2.4 1.64 0 .84.65 1.39 2.67 1.91s4.18 1.39 4.18 3.91c-.01 1.83-1.38 2.83-3.12 3.16z"/>
            </svg>
          </div>
        </div>
        
        {dataFetchFailed && (
          <div className="data-refresh-container">
            <p className="data-fetch-error">{t("main.contractDataError")}</p>
            <p className="data-fetch-explanation">{t("main.contractAddressIssue")}</p>
            <button 
              className="refresh-all-btn" 
              onClick={refreshAllContractData}
              disabled={isLoading.prizeAmount}
            >
              {isLoading.prizeAmount ? (
                <span className="spinner sm"></span>
              ) : (
                <>
                  <svg className="refresh-icon" viewBox="0 0 24 24">
                    <path d="M17.65 6.35A7.958 7.958 0 0012 4c-4.42 0-7.99 3.58-7.99 8s3.57 8 7.99 8c3.73 0 6.84-2.55 7.73-6h-2.08A5.99 5.99 0 0112 18c-3.31 0-6-2.69-6-6s2.69-6 6-6c1.66 0 3.14.69 4.22 1.78L13 11h7V4l-2.35 2.35z"></path>
                  </svg>
                  {t("main.refreshData")}
                </>
              )}
            </button>
          </div>
        )}
        
        <div className="prize-amount-wrapper">
          <div className="prize-amount">
            {prizeAmount ? (
              <div className="prize-value-container">
                <div className="prize-currency">
                  <span className="eth-icon">Ξ</span>
                </div>
                <div className="prize-value">
                  <span className="amount">{parseFloat(prizeAmount).toFixed(4)}</span>
                  <span className="amount-label">ETH</span>
                </div>
              </div>
            ) : (
              <button 
                className="refresh-btn" 
                onClick={getPrizeAmount}
                disabled={isLoading.prizeAmount}
              >
                {isLoading.prizeAmount ? (
                  <span className="spinner sm"></span>
                ) : (
                  <svg className="refresh-icon" viewBox="0 0 24 24">
                    <path d="M17.65 6.35A7.958 7.958 0 0012 4c-4.42 0-7.99 3.58-7.99 8s3.57 8 7.99 8c3.73 0 6.84-2.55 7.73-6h-2.08A5.99 5.99 0 0112 18c-3.31 0-6-2.69-6-6s2.69-6 6-6c1.66 0 3.14.69 4.22 1.78L13 11h7V4l-2.35 2.35z"></path>
                  </svg>
                )}
              </button>
            )}
          </div>
          
          {prizeAmount && (
            <div className="prize-info">
              <div className="prize-detail">
                <svg className="prize-detail-icon" viewBox="0 0 24 24">
                  <path d="M19 3h-4.18C14.4 1.84 13.3 1 12 1c-1.3 0-2.4.84-2.82 2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm-7 0c.55 0 1 .45 1 1s-.45 1-1 1-1-.45-1-1 .45-1 1-1zm-2 14l-4-4 1.41-1.41L10 14.17l6.59-6.59L18 9l-8 8z"></path>
                </svg>
                <span>{t("main.verifiedPrizePool")}</span>
              </div>
              
              <div className="prize-detail">
                <svg className="prize-detail-icon" viewBox="0 0 24 24">
                  <path d="M11.99 2C6.47 2 2 6.48 2 12s4.47 10 9.99 10C17.52 22 22 17.52 22 12S17.52 2 11.99 2zM12 20c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8zm.5-13H11v6l5.25 3.15.75-1.23-4.5-2.67z"></path>
                </svg>
                <span>{t("main.updatedNow")}</span>
              </div>
            </div>
          )}
        </div>
        
        <div className="prize-card-footer">
          <div className="prize-card-action">
            <button 
              className={`prize-action-btn ${!account || !isOpenLottery ? 'disabled' : ''}`}
              onClick={buyTicket}
              disabled={!account || isLoading.buyTicket || !isOpenLottery}
            >
              <svg className="action-icon" viewBox="0 0 24 24">
                <path d="M19 13h-6v6h-2v-6H5v-2h6V5h2v6h6v2z"></path>
              </svg>
              <span>{t("main.buyTicket")}</span>
            </button>
            
            {account && !isOpenLottery && (
              <div className="lottery-status-message error">
                <svg className="warning-icon-sm" viewBox="0 0 24 24">
                  <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-2h2v2zm0-4h-2V7h2v6z"></path>
                </svg>
                <span>{t("main.lotteryNotOpen")}</span>
              </div>
            )}

            {buyMessage && (
              <div style={{marginTop: "10px"}} className={`message ${buyMessageType === 'success' ? 'success' : 'error'}`}>
                {buyMessage}
              </div>
            )}
            
            {account && isOpenLottery && stopTime && (
              <div className="lottery-status-message">
                <span>
                  {Math.floor(Date.now()/1000) < Number(stopTime) ? 
                    t("main.salesEndIn", { time: formatTimeRemaining(stopTime) }) : 
                    t("main.lotteryEnded")
                  }
                </span>
              </div>
            )}
          </div>
          
          <div className="prize-card-decoration bottom">
            <div className="decoration-pulse"></div>
          </div>
        </div>
      </div>
      
      <div className="lottery-status">
        <div className="status-item">
          <div className="status-icon-wrapper">
            <svg className="status-icon" viewBox="0 0 24 24">
              <path d="M20 4H4c-1.11 0-1.99.89-1.99 2L2 18c0 1.11.89 2 2 2h16c1.11 0 2-.89 2-2V6c0-1.11-.89-2-2-2zm0 14H4v-6h16v6zm0-10H4V6h16v2zm-6 8h4v-3h-4v3z"/>
            </svg>
            <div className="status-icon-bg"></div>
          </div>
          <div className="status-info">
            <h3>{t("main.getTicketCount")}</h3>
            <div className="status-value-container">
              <p className="status-value">
                {ticketCount !== null ? (
                  <span className="highlight-value">{ticketCount.toString()}</span>
                ) : (
                  <button 
                    className="text-btn" 
                    onClick={getTicketCount}
                    disabled={isLoading.ticketCount}
                  >
                    {isLoading.ticketCount ? (
                      <span className="spinner sm"></span>
                    ) : "..."}
                  </button>
                )}
              </p>
              <span className="status-label">{t("main.tickets")}</span>
            </div>
            {ticketCount !== null && Number(ticketCount) > 0 && (
              <div className="status-progress-bar">
                <div className="status-progress" style={{width: `${Math.min(100, Number(ticketCount) * 2)}%`}}></div>
              </div>
            )}
          </div>
        </div>
        
        <div className="status-item">
          <div className="status-icon-wrapper">
            <svg className="status-icon" viewBox="0 0 24 24">
              <path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41L9 16.17z"/>
            </svg>
            <div className="status-icon-bg"></div>
          </div>
          <div className="status-info">
            <h3>{t("main.getIsOpenLottery")}</h3>
            <div className="status-value-container">
              <p className={`status-value ${isOpenLottery ? 'status-success' : 'status-error'}`}>
                {isOpenLottery !== null ? (
                  <span className="lottery-status-indicator">
                    <span className={`status-dot ${isOpenLottery ? 'dot-active' : 'dot-inactive'}`}></span>
                    {isOpenLottery ? t("main.yes") : t("main.no")}
                  </span>
                ) : (
                  <button 
                    className="text-btn" 
                    onClick={getIsOpenLottery}
                    disabled={isLoading.isOpenLottery}
                  >
                    {isLoading.isOpenLottery ? (
                      <span className="spinner sm"></span>
                    ) : "..."}
                  </button>
                )}
              </p>
              {isOpenLottery !== null && (
                <span className="status-label">
                  {isOpenLottery ? t("main.activeNow") : t("main.closed")}
                </span>
              )}
            </div>
            {isOpenLottery !== null && (
              <div className="lottery-status-message">
                {isOpenLottery ? (
                  <span className="lottery-open-message">{t("main.ticketsAvailableForPurchase")}</span>
                ) : (
                  <span className="lottery-closed-message">{t("main.waitingForNextRound")}</span>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </section>
  );
};

export default LotteryOverview; 