// GeoLocationUtils.js

export const getCountryFromAPI = async () => {
    try {
      const response = await fetch('https://api.ip.sb/geoip');
      const data = await response.json();
  
      // 返回国家代码（例如 'US'）
      return data.country_code.toUpperCase() || 'defaultCountry';  // 默认返回 'US' 如果无法获取
    } catch (error) {
      //console.error('Error fetching country information:', error);
      return 'defaultCountry';  // 如果发生错误，默认返回美国
    }
  };


export const getMyTickets = async (page, pageSize, account) => {
    try {
        const response = await fetch(`https://sys.youtowin.com/api/user/myTicketPage?page=${page}&pageSize=${pageSize}&address=${account}`);
        const data = await response.json();
        if(data.code === 0) {
            return data.result.records;
        } else {
            return [];
        }
    } catch (error) {
        //console.error('Error fetching my tickets:', error);
        return [];
    }
}

export const getAgentsPage = async (page, pageSize, account) => {
    try {
        const response = await fetch(`https://sys.youtowin.com/api/agents/page?page=${page}&pageSize=${pageSize}&address=${account}`);
        const data = await response.json();
        if(data.code === 0) {
            return data.result;
        } else {
            return null;
        }
    } catch (error) {
        //console.error('Error fetching agents:', error);
        return null;
    }
}