import { useTranslation } from 'react-i18next';

/**
 * Formats an address for display by truncating the middle
 * @param {string} address - The wallet address to format
 * @returns {string} Formatted address
 */
export const formatAddress = (address) => {
  if (!address) return "";
  return `${address.substring(0, 8)}...${address.substring(address.length - 6)}`;
};

/**
 * Formats a timestamp to a readable date string
 * @param {number|string|BigInt} timestamp - Unix timestamp in seconds
 * @returns {string} Formatted date string
 */
export const formatTimestamp = (timestamp) => {
  if (!timestamp) return "";
  // 确保timestamp转换为Number类型
  const timeNum = Number(timestamp.toString());
  const date = new Date(timeNum * 1000);
  return date.toLocaleString();
};

/**
 * Formats a date string to a readable string
 * @param {string} dateString - Date string
 * @returns {string} Formatted date string or "-" if no date
 */
export const formatDateString = (dateString) => {
  if (!dateString) return "-";
  const date = new Date(dateString);
  return date.toLocaleString();
};

/**
 * Formats the remaining time until a timestamp
 * @param {number|string|BigInt} timestamp - Unix timestamp in seconds
 * @returns {string} Formatted time remaining string
 */
export const formatTimeRemaining = (timestamp, t) => {
  if (!timestamp) return "";
  
  const now = Math.floor(Date.now() / 1000);
  const timeLeft = Number(timestamp.toString()) - now;
  
  if (timeLeft <= 0) return t("main.ended");
  
  const hours = Math.floor(timeLeft / 3600);
  const minutes = Math.floor((timeLeft % 3600) / 60);
  
  if (hours > 0) {
    return `${hours}${t("main.hours")} ${minutes}${t("main.minutes")}`;
  } else {
    return `${minutes}${t("main.minutes")}`;
  }
};

/**
 * Formats ticket indexes for display
 * @param {Array} tickets - Array of ticket objects
 * @returns {string} Formatted ticket indexes
 */
export const formatIndex = (tickets) => {
  if (!tickets || tickets.length === 0) return "-";
  const indexes = tickets.map(ticket => `#${ticket.index}`);
  if (indexes.length <= 2) {
    return indexes.join(', ');
  } else {
    // If there are many tickets, only show the first 2
    return `${indexes.slice(0, 2).join(', ')} + ${indexes.length - 2} more`;
  }
};

/**
 * Creates tooltip text for tickets
 * @param {Array} tickets - Array of ticket objects
 * @returns {string} Tooltip text
 */
export const createTicketTooltip = (tickets) => {
  if (!tickets || tickets.length === 0) return "";
  
  const indexes = tickets.map(ticket => `#${ticket.index}`);
  if (indexes.length <= 5) {
    return indexes.join(', ');
  } else {
    // If there are many tickets, only show the first 5
    return `${indexes.slice(0, 5).join(', ')} + ${indexes.length - 5} more`;
  }
}; 