import { ethers } from 'ethers';
import WalletConnectService from './WalletConnectService';
import { infuraKey } from '../utils/GloabStringUtils';

// 创建自定义事件系统，用于通知App组件状态变化
class EventEmitter {
  constructor() {
    this.events = {};
  }
  
  on(event, listener) {
    if (!this.events[event]) {
      this.events[event] = [];
    }
    this.events[event].push(listener);
    return () => this.removeListener(event, listener);
  }
  
  removeListener(event, listener) {
    if (!this.events[event]) return;
    this.events[event] = this.events[event].filter(l => l !== listener);
  }
  
  emit(event, ...args) {
    if (!this.events[event]) return;
    this.events[event].forEach(listener => listener(...args));
  }
}

// 添加常量定义（在类定义上方）
const EXPECTED_CHAIN_ID = '0x1'; // Ethereum主网的chainId，十六进制格式
const EXPECTED_CHAIN_NAME = 'Ethereum Mainnet';

// 改为支持多网络
// const SUPPORTED_CHAINS = {
//   '0x1': 'Ethereum Mainnet',
//   '0x5': 'Goerli Testnet',
//   '0xaa36a7': 'Sepolia Testnet',
//   '0x13881': 'Mumbai Testnet',
//   '0x89': 'Polygon Mainnet',
//   '0xa': 'Optimism Mainnet',
//   '0xa4b1': 'Arbitrum One'
// };

class WalletService extends EventEmitter {
  constructor() {
    super();
    this.provider = null;
    this.signer = null;
    this.account = null;
    this.walletType = null; // 'metamask' 或 'walletconnect'
    this.connecting = false;
    this.connectionError = null;
    
    // 检查是否存在保存的会话
    this.checkSavedSession();
  }
  
  // 检查保存的会话
  checkSavedSession() {
    // 如果是WalletConnect，从localStorage中检查会话
    const hasWalletConnectSession = 
      localStorage.getItem('walletconnect') !== null ||
      localStorage.getItem('wc@2:client:0.3') !== null;
    
    if (hasWalletConnectSession) {
      //console.log('Found WalletConnect saved session');
      // 不自动恢复，只标记存在会话
      this.hasStoredSession = true;
    }
  }

  // 检测设备类型
  isMobileDevice() {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  }

  // 检查是否有MetaMask或其他浏览器钱包
  hasBrowserWallet() {
    return window.ethereum !== undefined;
  }
  
  // 检查是否支持深度链接直接唤起钱包
  supportsMobileDeepLink() {
    // 检测是否为iOS设备并且在Safari浏览器中
    const isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
    const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    
    // 判断是否有已知支持的钱包应用
    const hasMetaMask = typeof window.ethereum?.isMetaMask !== 'undefined';
    const hasCoinbaseWallet = typeof window.ethereum?.isCoinbaseWallet !== 'undefined';
    
    // iOS Safari 和 已知钱包支持深度链接
    return (isIOS && isSafari) || hasMetaMask || hasCoinbaseWallet;
  }

  async connectWallet() {
    if (this.connecting) {
      //console.log('Already attempting to connect wallet');
      return null;
    }
    
    try {
      this.connectionError = null;
      this.connecting = true;
      this.emit('connecting', true);
      
      const isMobile = this.isMobileDevice();
      const hasBrowserWallet = this.hasBrowserWallet();
      const supportsDeepLink = this.supportsMobileDeepLink();
      
      //console.log(`Device info: Mobile: ${isMobile}, Browser wallet: ${hasBrowserWallet}, Deep link: ${supportsDeepLink}`);
      
      // 移动设备且没有浏览器钱包，或明确需要使用WalletConnect
      if ((isMobile && !hasBrowserWallet) || this.hasStoredSession) {
        return await this.connectWithWalletConnect();
      } else {
        try {
          // 尝试使用浏览器钱包
          return await this.connectWithBrowserWallet();
        } catch (error) {
          //console.log('Browser wallet connection failed, trying WalletConnect...', error);
          // 浏览器钱包连接失败，尝试WalletConnect
          return await this.connectWithWalletConnect();
        }
      }
    } catch (error) {
      //console.error('Failed to connect wallet:', error);
      this.connectionError = error.message || 'Unknown connection error';
      this.emit('error', this.connectionError);
      throw error;
    } finally {
      this.connecting = false;
      this.emit('connecting', false);
    }
  }

  async connectWithBrowserWallet() {
    try {
      if (!window.ethereum) {
        const error = new Error('No browser wallet found');
        this.emit('error', error.message);
        throw error;
      }

      // 使用ethers连接浏览器钱包
      this.provider = new ethers.BrowserProvider(window.ethereum);
      const accounts = await this.provider.send('eth_requestAccounts', []);
      
      if (!accounts || accounts.length === 0) {
        const error = new Error('No accounts returned from wallet');
        this.emit('error', error.message);
        throw error;
      }
      
      this.account = accounts[0];
      this.signer = await this.provider.getSigner();
      this.walletType = 'metamask';

      // 设置事件监听器
      this.setupBrowserWalletEvents();
      
      // 发送连接成功事件
      this.emit('connected', { account: this.account, type: this.walletType });

      return this.account;
    } catch (error) {
      //console.error('Failed to connect browser wallet:', error);
      // 确保错误被传播
      this.emit('error', error.message || 'Browser wallet connection failed');
      throw error;
    }
  }

  async connectWithWalletConnect() {
    try {
      // 设置状态和发送事件
      this.emit('walletconnect_started');
      
      // 获取WalletConnect的账户
      this.account = await WalletConnectService.connect();
      
      if (!this.account) {
        const error = new Error('No account returned from WalletConnect');
        this.emit('error', error.message);
        throw error;
      }
      
      // 获取WalletConnect provider并创建ethers provider
      const wcProvider = WalletConnectService.getProvider();
      if (!wcProvider) {
        const error = new Error('WalletConnect provider not initialized');
        this.emit('error', error.message);
        throw error;
      }
      
      this.provider = new ethers.BrowserProvider(wcProvider);
      this.signer = await this.provider.getSigner();
      this.walletType = 'walletconnect';
      
      // 发送连接成功事件
      this.emit('connected', { account: this.account, type: this.walletType });

      return this.account;
    } catch (error) {
      //console.error('Failed to connect with WalletConnect:', error);
      // 发送自定义错误事件
      this.emit('error', error.message || 'WalletConnect connection failed');
      throw error;
    } finally {
      this.emit('walletconnect_ended');
    }
  }

  setupBrowserWalletEvents() {
    if (!window.ethereum) return;

    window.ethereum.on('accountsChanged', (accounts) => {
      if (accounts.length === 0) {
        // 用户断开了钱包连接
        this.disconnect();
        this.emit('disconnected');
      } else {
        // 账户变更
        this.account = accounts[0];
        this.emit('accountChanged', accounts[0]);
      }
    });

    window.ethereum.on('chainChanged', (chainId) => {
      // 链变更
      this.emit('chainChanged', chainId);
      // 刷新页面以确保一切正常工作
      window.location.reload();
    });
    
    window.ethereum.on('disconnect', () => {
      this.disconnect();
      this.emit('disconnected');
    });
  }

  async disconnect() {
    try {
      if (this.walletType === 'walletconnect') {
        await WalletConnectService.disconnect();
      }
      
      // 重置状态
      this.provider = null;
      this.signer = null;
      this.account = null;
      this.walletType = null;
      
      // 发送断开连接事件
      this.emit('disconnected');
      
      // 清除localStorage中的会话
      if (localStorage.getItem('walletconnect')) {
        localStorage.removeItem('walletconnect');
      }
      if (localStorage.getItem('wc@2:client:0.3')) {
        localStorage.removeItem('wc@2:client:0.3');
      }
      
      this.hasStoredSession = false;
    } catch (error) {
      //console.error('Error disconnecting wallet:', error);
      this.emit('error', error.message || 'Failed to disconnect wallet');
      throw error;
    }
  }

  getProvider() {
    return this.provider;
  }

  getSigner() {
    return this.signer;
  }

  getAccount() {
    return this.account;
  }
  
  /**
   * Gets the WalletConnect provider instance
   * @returns The WalletConnect provider
   */
  getWalletConnectProvider() {
    return WalletConnectService.getProvider();
  }

  isConnected() {
    return !!this.account;
  }

  // 创建合约实例
  async getContract(address, abi) {
    if (!this.signer) {
      throw new Error('Wallet not connected');
    }
    
    // 确保钱包在正确的网络上
    await this.verifyChainId();
    
    // 判断是WalletConnect还是MetaMask，做不同处理
    if (this.walletType === 'walletconnect') {
      //console.log('使用WalletConnect创建合约实例...');
      
      try {
        // 为WalletConnect使用JsonRpcProvider而不是BrowserProvider
        // 这可以解决某些移动端兼容性问题
        const wcProvider = WalletConnectService.getProvider();
        
        // 记录provider详情用于调试
        //console.log('WalletConnect provider details:', {
        //   connected: wcProvider.connected,
        //   chainId: wcProvider.chainId,
        //   accounts: wcProvider.accounts
        // });
        
        // 创建静态Provider用于只读调用
        // const staticProvider = new ethers.JsonRpcProvider('https://mainnet.infura.io/v3/' + infuraKey());
        const staticProvider = new ethers.JsonRpcProvider('https://eth.llamarpc.com');
        
        // 确认Signer的可用性
        const signer = this.signer;
        //console.log('WalletConnect Signer可用性:', !!signer);
        
        // 创建合约实例，同时支持交易操作
        //console.log('创建支持交易和读取的合约实例...');
        const contract = new ethers.Contract(address, abi, staticProvider);
        
        // 获取原始地址，用于检查
        //console.log('合约地址:', address);
        
        // 对于交易操作，连接Signer
        const contractWithSigner = contract.connect(signer);
        //console.log('已成功连接Signer到合约');
        
        return contractWithSigner;
      } catch (error) {
        //console.error('WalletConnect创建合约失败:', error);
        throw error;
      }
    } else {
      // MetaMask或其他浏览器钱包使用标准方式
      //console.log('使用MetaMask/浏览器钱包创建合约实例...');
      return new ethers.Contract(address, abi, this.signer);
    }
  }
  
  // 验证当前连接的链ID
  async verifyChainId() {
    try {
      if (!this.provider) {
        throw new Error('No provider available');
      }
      
      // 获取当前网络
      const network = await this.provider.getNetwork();
      const currentChainId = '0x' + network.chainId.toString(16);
      //console.log('Current chain ID:', currentChainId, 'Expected:', EXPECTED_CHAIN_ID);
      
      // 链ID不匹配时发出事件，但不抛出错误以便应用可以处理
      if (currentChainId !== EXPECTED_CHAIN_ID) {
        this.emit('wrongNetwork', {
          current: currentChainId,
          expected: EXPECTED_CHAIN_ID,
          name: EXPECTED_CHAIN_NAME
        });
        //console.warn(`Connected to wrong network. Expected ${EXPECTED_CHAIN_ID} (${EXPECTED_CHAIN_NAME}), got ${currentChainId}`);
        
        // 如果是浏览器钱包，尝试切换网络
        if (this.walletType === 'metamask' && window.ethereum) {
          try {
            await window.ethereum.request({
              method: 'wallet_switchEthereumChain',
              params: [{ chainId: EXPECTED_CHAIN_ID }],
            });
            // 切换成功后刷新页面
            window.location.reload();
          } catch (switchError) {
            // 无法切换网络
            //console.error('Failed to switch network:', switchError);
          }
        }
      }
    } catch (error) {
      //console.error('Error verifying chain ID:', error);
      throw new Error('Could not verify blockchain network');
    }
  }
}

// 导出单例实例
export default new WalletService(); 