  async getAllAgentCommissions() {
    try {
      let commissions;
  
      if (!this.isMobile && WalletService.walletType !== 'walletconnect') {
        const contract = await this.getNativeContract();
        commissions = await contract.getAllAgentCommissions();
      } else {
        const readOnlyContract = await this.getReadOnlyContract();
        commissions = await readOnlyContract.getAllAgentCommissions();
      }
  
      // 转换为标准 JSON 格式
      const jsonData = commissions.map(info => ({
        country: info.countryCode,
        address: info.agentAddress,
        balance: Number(info.commissionBalance) // 或 ethers.formatEther(info.commissionBalance)
      }));
  
      return jsonData;
    } catch (error) {
      console.error("Error fetching all agent commissions:", error);
      throw error;
    }
  }

