import { ethers } from "ethers";
import { useState, useEffect, useRef } from "react";
import { useTranslation } from "react-i18next";
import { getCountryFromAPI, getMyTickets } from "./utils/GeoLocationUtils";
import { formatAddress, formatTimestamp, formatDateString, formatTimeRemaining, formatIndex } from "./utils/FormatUtils";
// import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import "./i18n";
import "./App.css";
import WalletService from "./services/WalletService";
import ContractService from "./services/ContractService";

// Import components
import Header from "./components/Header";
import Footer from "./components/Footer";
import ConnectPrompt from "./components/ConnectPrompt";
import LotteryOverview from "./components/LotteryOverview";
import TicketHistory from "./components/TicketHistory";
import Admin from "./components/Admin";

function MainApp() {
  const { t, i18n } = useTranslation();
  const [language, setLanguage] = useState('en');
  const [isConnecting, setIsConnecting] = useState(false);
  const [account, setAccount] = useState(null);
  const [countryCode, setCountryCode] = useState(""); // 初始为空字符串
  const isFirstRender = useRef(true); // 使用 ref 来标记第一次渲染
  const [prizeAmount, setPrizeAmount] = useState(0);
  const [buyMessage, setBuyMessage] = useState(null);
  const [buyMessageType, setBuyMessageType] = useState(""); // 'success' or 'error'
  const [ticketCount, setTicketCount] = useState(null);
  const [lastWinner, setLastWinner] = useState(null);
  const [isOpenLottery, setIsOpenLottery] = useState(null);
  const [stopTime, setStopTime] = useState(null);
  const [drawTime, setDrawTime] = useState(null);
  const [connectWalletText, setConnectWalletText] = useState("");
  const [isLoading, setIsLoading] = useState({}); // 跟踪按钮加载状态
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 480);
  const [networkError, setNetworkError] = useState(null);
  const [dataFetchFailed, setDataFetchFailed] = useState(false);
  const [myTickets, setMyTickets] = useState([]);
  const [currentRound, setCurrentRound] = useState(null);
  const vrfUrl = "https://vrf.chain.link/mainnet#/side-drawer/subscription/mainnet/10992236483715826184027828765430125791284498914240650122053406428111316319008";

  const [searchParams, setSearchParams] = useState({
    page: 1,
    pageSize: 10
  });

  // 添加历史记录过滤状态
  const [historyFilter, setHistoryFilter] = useState('all');

  useEffect(() => {
    // 使用 URLSearchParams 获取查询参数
    const params = new URLSearchParams(window.location.search);
    // 获取 'code' 参数的值
    const code = params.get('code');
    if (code) {
      const codeUpperCase = code.toUpperCase();
      localStorage.setItem("countryCode", codeUpperCase);
      setCountryCode(codeUpperCase); // 设置获取到的 code 值
    }
  }, []); // 只在组件加载时执行一次

  useEffect(() => {
    // Check if countryCode is already stored in localStorage
    const storedCountryCode = localStorage.getItem("countryCode");
    // 如果已有 countryCode 存在于 localStorage，直接设置，不再请求
    if (storedCountryCode) {
      setCountryCode(storedCountryCode);
    } else {
      // 如果没有存储 countryCode，第一次请求时才发起 API 请求
      if (isFirstRender.current) {
        getCountryFromAPI().then((code) => {
          localStorage.setItem("countryCode", code);
          setCountryCode(code);
        });
        isFirstRender.current = false; // 只有第一次请求时，才执行请求
      }
    }
  }, []); // 空依赖数组，确保只在组件挂载时执行一次

  // 监听窗口大小变化
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 480);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    if (account) {
      getMyTickets(searchParams.page, searchParams.pageSize, account).then((tickets) => {
        setMyTickets(tickets);
      });
    }
  }, [account, searchParams.page, searchParams.pageSize]);

  // 监听WalletService的事件
  useEffect(() => {
    
    const wrongNetworkHandler = (data) => {
      setNetworkError({
        current: data.current,
        expected: data.expected,
        name: data.name
      });
    };
    
    const connectedHandler = () => {
      setNetworkError(null);
    };
    
    // 注册事件监听器
    WalletService.on('wrongNetwork', wrongNetworkHandler);
    WalletService.on('connected', connectedHandler);
    
    // 清理事件监听器
    return () => {
      WalletService.removeListener('wrongNetwork', wrongNetworkHandler);
      WalletService.removeListener('connected', connectedHandler);
    };
  }, []);

  async function connectWallet() {
    try {
      setIsConnecting(true);
      
      // 使用新的WalletService替代原来的连接逻辑
      const connectedAccount = await WalletService.connectWallet();
      setAccount(connectedAccount);
      setConnectWalletText(t("main.connectWallet"));
      
      // 先进行合约检查
      await checkContractExists();
      
      // 检查彩票当前状态
      await checkLotteryStatus();
      
      // 连接钱包后自动获取所有数据
      await Promise.all([
        getPrizeAmount(),
        getTicketCount(),
        getCurrentRound(),
        getIsOpenLottery(),
        getStopTime(),
        getDrawTime(),
        getLastWinner()
      ]);
    } catch (error) {
      //console.error("Error connecting wallet:", error);
      alert(error);

      setConnectWalletText(t("main.connectWalletError"));
      setIsConnecting(false);
    }
  }

  // 新增函数：检查合约地址是否存在
  async function checkContractExists() {
    try {
      const provider = WalletService.getProvider();
      if (!provider) {
        throw new Error("Provider not available");
      }
      
      const contractAddress = ContractService.contractAddress;
      //console.log(`正在检查合约地址: ${contractAddress}`);
      // 获取合约代码
      const code = await provider.getCode(contractAddress);
      //console.log('合约代码长度:', code.length);
      
      // 如果没有代码，说明合约不存在
      if (code === '0x' || code === '0x0') {
        //console.error('合约地址没有代码，合约可能不存在或者不在当前网络上');
        setDataFetchFailed(true);
        return false;
      }
      
      //console.log('合约存在，有部署的代码');
      return true;
    } catch (error) {
      //console.error('检查合约时出错:', error);
      setDataFetchFailed(true);
      return false;
    }
  }

  async function buyTicket() {
    // 使用ethers.parseEther安全地转换为BigInt (Wei)
    const ticketAmountInWei = ethers.parseEther("0.01"); 
    setBuyMessage(null);
    
    setIsLoading(prev => ({ ...prev, buyTicket: true }));
    try {
      //console.log("开始购票流程...");

      try {
        if (!isMobile && WalletService.walletType !== 'walletconnect') {
          const contract = await ContractService.getContract();
          const tx = await contract.buyTicket(countryCode, { value: ticketAmountInWei });
          await tx.wait();
          setBuyMessage(t("main.buyTicketSuccess"));
          setBuyMessageType("success");
          getTicketCount();
          getPrizeAmount();
          setIsLoading(prev => ({ ...prev, buyTicket: false }));
          return;
        }
      } catch (error) {
        //console.error("PC设备购票失败:", error);
        setBuyMessage(t("main.buyTicketError"));
        setBuyMessageType("error");
        setIsLoading(prev => ({ ...prev, buyTicket: false }));
        return;
      }
      
      // 获取当前钱包类型
      const walletType = WalletService.walletType;
      //console.log("当前钱包类型:", walletType);
      
      // =======================================================
      // 移动端WalletConnect购票流程
      // =======================================================
      if (isMobile || walletType === 'walletconnect') {
        //console.log("使用移动端专用购票流程...");
        
        try {
          // 1. 获取原始钱包提供者和当前账户
          const wcProvider = WalletService.getWalletConnectProvider();
          if (!wcProvider || !wcProvider.connected) {
            throw new Error("WalletConnect未连接");
          }
          
          const currentAccount = account || wcProvider.accounts[0];
          if (!currentAccount) {
            throw new Error("未找到钱包账户");
          }
          //console.log("使用账户:", currentAccount);
          
          // 2. 获取钱包当前链ID
          const chainId = wcProvider.chainId;
          //console.log("当前链ID:", chainId);
          
          // 3. 获取合约ABI中buyTicket函数的编码
          const contractInterface = new ethers.Interface(ContractService.contractABI);
          const data = contractInterface.encodeFunctionData("buyTicket", [countryCode]);
          //console.log("已编码的交易数据:", data);
          
          // 4. 创建底层交易对象
          const txParams = {
            from: currentAccount,
            to: ContractService.contractAddress,
            value: "0x2386f26fc10000", // 0.01 ETH in hex
            data: data,
            chainId: chainId
          };
          //console.log("构造的交易参数:", txParams);
          
          // 5. 直接通过WalletConnect发送交易
          //console.log("正在发送交易...");
          const txHash = await wcProvider.request({
            method: 'eth_sendTransaction',
            params: [txParams],
          });
          
          //console.log("交易已发送，哈希:", txHash);
          setBuyMessage(t("main.buyTicketSubmitted"));
          setBuyMessageType("success");
          
          // 6. 获取交易回执
          try {
            // 创建一个静态provider来获取交易回执
            const staticProvider = new ethers.JsonRpcProvider('https://eth.llamarpc.com');
            
            //console.log("等待交易确认...");
            // 轮询交易回执
            let receipt = null;
            let retries = 0;
            
            while (!receipt && retries < 10) {
              try {
                receipt = await staticProvider.getTransactionReceipt(txHash);
                if (receipt) {
                  //console.log("交易已确认，回执:", receipt);
                  setBuyMessage(t("main.buyTicketSuccess"));
                  // 更新票数和奖金
                  getTicketCount();
                  getPrizeAmount();
                  break;
                }
              } catch (e) {
                //console.log("等待交易确认中...");
              }
              
              retries++;
              // 等待5秒再次检查
              await new Promise(r => setTimeout(r, 5000));
            }
            
            if (!receipt) {
              //console.log("交易已提交，但尚未确认");
              setBuyMessage(t("main.buyTicketSubmitted"));
            }
          } catch (receiptError) {
            //console.error("获取交易回执失败:", receiptError);
            // 这里不设置错误消息，因为交易已经提交
          }
          
          setIsLoading(prev => ({ ...prev, buyTicket: false }));
          return;
        } catch (mobileError) {
          //console.error("移动端购票流程失败:", mobileError);
          
          if (mobileError.message) {
            //console.log("错误信息:", mobileError.message);
            
            // 检查特定错误类型
            if (mobileError.message.includes("rejected")) {
              setBuyMessage(t("main.userRejected"));
              setBuyMessageType("error");
              setIsLoading(prev => ({ ...prev, buyTicket: false }));
              return;
            }
          }
        }
      }
      
      // ===============================================
      // PC端MetaMask标准购票流程
      // ===============================================
      //console.log("使用PC端标准方法发送交易...");
      
      try {
        // 获取PC端合约实例
        const signer = WalletService.getSigner();
        if (!signer) {
          throw new Error("No signer available");
        }
        
        const contract = new ethers.Contract(
          ContractService.contractAddress, 
          ContractService.contractABI, 
          signer
        );
        //console.log("PC端合约实例创建成功:", contract.target);
        
        // 检查合约函数是否存在
        if (typeof contract.buyTicket !== 'function') {
          //console.error("合约方法不存在:", "buyTicket");
          throw new Error("合约中没有找到buyTicket方法");
        }
        
        // 正常调用buyTicket
        const tx = await contract.buyTicket(countryCode, { 
          value: ticketAmountInWei,
          gasLimit: 300000 // 设置足够高的gas限制
        });
        //console.log("交易已发送，等待确认...");
        await tx.wait();
        //console.log("交易已确认");
        
        setBuyMessage(t("main.buyTicketSuccess"));
        setBuyMessageType("success");
        // 更新票数和奖金
        getTicketCount();
        getPrizeAmount();
      } catch (error) {
        throw error;
      }
    } catch (error) {
      //console.error("Error buying ticket:", error);
      
      // 更详细的错误日志
      if (error.message) {
        //console.log("错误信息:", error.message);
      }
      if (error.code) {
        //console.log("错误代码:", error.code);
      }
      if (error.transaction) {
        //console.log("交易信息:", error.transaction);
      }
      
      // 针对常见错误提供更具体的提示
      let errorMessage = t("main.buyTicketError");
      if (error.message?.includes("user rejected")) {
        errorMessage = t("main.userRejected");
      } else if (error.message?.includes("insufficient funds")) {
        errorMessage = t("main.insufficientFunds");
      } else if (error.message?.includes("lottery is not open")) {
        errorMessage = t("main.lotteryNotOpen");
        // 刷新彩票状态
        checkLotteryStatus();
      } else if (error.message?.includes("lottery ended") || error.message?.includes("ticket sales ended")) {
        errorMessage = t("main.lotteryEnded");
        // 刷新彩票状态
        checkLotteryStatus();
      } else if (error.message?.includes("not a function")) {
        errorMessage = t("main.contractMethodError");
      } else {
        // 其他错误情况下也刷新彩票状态
        checkLotteryStatus();
      }
      
      setBuyMessage(errorMessage);
      setBuyMessageType("error");
    } finally {
      setIsLoading(prev => ({ ...prev, buyTicket: false }));
    }
  }

  async function getPrizeAmount() {
    setIsLoading(prev => ({ ...prev, prizeAmount: true }));
    setDataFetchFailed(false);
    try {
      const prizeAmountBN = await ContractService.getPrizeAmount();
      const etherAmount = ethers.formatEther(prizeAmountBN);
      setPrizeAmount(etherAmount);
    } catch (error) {
      //console.error("Error fetching prize amount:", error);
      setDataFetchFailed(true);
    } finally {
      setIsLoading(prev => ({ ...prev, prizeAmount: false }));
    }
  }

  async function getTicketCount() {
    setIsLoading(prev => ({ ...prev, ticketCount: true }));
    try {
      const count = await ContractService.getTicketCount();
      setTicketCount(count);
    } catch (error) {
      //console.error("Error fetching ticket count:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, ticketCount: false }));
    }
  }

  async function getLastWinner() {
    setIsLoading(prev => ({ ...prev, lastWinner: true }));
    try {
      const contract = await ContractService.getContract();
      const winner = await contract.getLastWinner();
      setLastWinner(winner);
    } catch (error) {
      //console.error("Error fetching last winner:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, lastWinner: false }));
    }
  }

  async function getIsOpenLottery() {
    setIsLoading(prev => ({ ...prev, isOpenLottery: true }));
    try {
      const contract = await ContractService.getContract();
      const isOpen = await contract.isOpenLottery();
      setIsOpenLottery(isOpen);
    } catch (error) {
      //console.error("Error fetching lottery status:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, isOpenLottery: false }));
    }
  }

  async function getStopTime() {
    setIsLoading(prev => ({ ...prev, stopTime: true }));
    try {
      const contract = await ContractService.getContract();
      const time = await contract.getStopTime();
      setStopTime(time);
    } catch (error) {
      //console.error("Error fetching stop time:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, stopTime: false }));
    }
  }

  async function getDrawTime() {
    setIsLoading(prev => ({ ...prev, drawTime: true }));
    try {
      const contract = await ContractService.getContract();
      const time = await contract.getDrawTime();
      setDrawTime(time);
    } catch (error) {
      //console.error("Error fetching draw time:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, drawTime: false }));
    }
  }

  async function getCurrentRound() {
    setIsLoading(prev => ({ ...prev, currentRound: true }));
    try {
      const round = await ContractService.getCurrentRound();
      setCurrentRound(round);
    } catch (error) {
      //console.error("Error fetching current round:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, currentRound: false }));
    }
  }

  // 添加断开钱包连接功能
  async function disconnectWallet() {
    try {
      await WalletService.disconnect();
      setAccount(null);
    } catch (error) {
      //console.error("Error disconnecting wallet:", error);
    }
  }

  // 添加一个重试所有数据的函数
  async function refreshAllContractData() {
    try {
      //console.log("Manually refreshing all contract data...");
      setDataFetchFailed(false);
      
      await Promise.all([
        getPrizeAmount(),
        getTicketCount(),
        getCurrentRound(),
        getIsOpenLottery(),
        getStopTime(),
        getDrawTime(),
        getLastWinner()
      ]);
    } catch (error) {
      //console.error("Error refreshing contract data:", error);
      setDataFetchFailed(true);
    }
  }

  // 添加一个直接检查彩票状态的方法，并在页面显示
  async function checkLotteryStatus() {
    try {
      const contract = await ContractService.getContract();
      const isOpen = await contract.isOpenLottery();
      setIsOpenLottery(isOpen);
      
      const stopTime = await contract.getStopTime();
      setStopTime(stopTime);
      
      const currentTime = Math.floor(Date.now() / 1000);
      const timeLeft = Number(stopTime) - currentTime;
      
      //console.log("彩票状态详情:");
      //console.log("- 开放状态:", isOpen);
      //console.log("- 停止时间:", new Date(Number(stopTime) * 1000).toLocaleString());
      //console.log("- 剩余时间:", timeLeft > 0 ? `${Math.floor(timeLeft / 3600)}小时${Math.floor((timeLeft % 3600) / 60)}分钟` : "已结束");
      
      return { isOpen, stopTime, timeLeft };
    } catch (error) {
      //console.error("检查彩票状态失败:", error);
      return null;
    }
  }

  // 查看票据详情
  const viewTicketDetails = () => {
    window.open(vrfUrl, '_blank');
  };

  // 添加格式化剩余时间的辅助函数
  const formatRemainingTime = (timestamp) => {
    return formatTimeRemaining(timestamp, t);
  };

  return (
    <div className="app-container">
      {/* 背景装饰 */}
      <div className="bg-decoration"></div>
      <div className="bg-cube cube1"></div>
      <div className="bg-cube cube2"></div>
      <div className="bg-cube cube3"></div>
      
      <Header 
        account={account}
        isConnecting={isConnecting}
        networkError={networkError}
        connectWallet={connectWallet}
        disconnectWallet={disconnectWallet}
        countryCode={countryCode}
        formatAddress={formatAddress}
      />

      <main className="main-content">
        {account ? (
          <>
            <LotteryOverview 
              prizeAmount={prizeAmount}
              ticketCount={ticketCount}
              isOpenLottery={isOpenLottery}
              stopTime={stopTime}
              isLoading={isLoading}
              account={account}
              buyTicket={buyTicket}
              buyMessage={buyMessage}
              buyMessageType={buyMessageType}
              getPrizeAmount={getPrizeAmount}
              getTicketCount={getTicketCount}
              getIsOpenLottery={getIsOpenLottery}
              refreshAllContractData={refreshAllContractData}
              dataFetchFailed={dataFetchFailed}
              formatTimeRemaining={formatRemainingTime}
            />
            
            <section className="lottery-actions">
              <div className="action-card">
                <div className="action-header">
                  <h2>{t("main.buyTicket")}</h2>
                  <div className="price-tag">0.01 ETH</div>
                </div>
                
                <button 
                  className="action-btn buy-btn" 
                  onClick={buyTicket} 
                  disabled={!account || isLoading.buyTicket || !isOpenLottery}
                >
                  {isLoading.buyTicket ? (
                    <>
                      <span className="spinner"></span>
                      {t("main.processing")}
                    </>
                  ) : (
                    <>
                      <svg className="ticket-icon" viewBox="0 0 24 24">
                        <path d="M22 10V6a2 2 0 0 0-2-2H4a2 2 0 0 0-2 2v4a2 2 0 1 1 0 4v4a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2v-4a2 2 0 1 1 0-4zm-10 7a3 3 0 1 1 0-6 3 3 0 0 1 0 6z"></path>
                      </svg>
                      {t("main.buyTicket")}
                    </>
                  )}
                </button>
                
                {buyMessage && (
                  <div className={`message ${buyMessageType === 'success' ? 'success' : 'error'}`}>
                    {buyMessage}
                  </div>
                )}
              </div>
            </section>
            
            <section className="lottery-details">
              <div className="card timeline-card">
                <h2>{t("main.lotteryTimeline")} UTC ( 20:00 )</h2>
                <div className="timeline">
                  <div className="timeline-item">
                    <div className="timeline-icon">
                      <svg viewBox="0 0 24 24">
                        <path d="M11.99 2C6.47 2 2 6.48 2 12s4.47 10 9.99 10C17.52 22 22 17.52 22 12S17.52 2 11.99 2zM12 20c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8zm.5-13H11v6l5.25 3.15.75-1.23-4.5-2.67z"></path>
                      </svg>
                    </div>
                    <div className="timeline-content">
                      <h3>{t("main.getStopTime")}</h3>
                      <p>
                        {stopTime ? (
                          formatTimestamp(stopTime)
                        ) : (
                          <button 
                            className="text-btn" 
                            onClick={getStopTime}
                            disabled={isLoading.stopTime}
                          >
                            {isLoading.stopTime ? (
                              <span className="spinner sm"></span>
                            ) : t("main.fetchData")}
                          </button>
                        )}
                      </p>
                    </div>
                  </div>
                  
                  <div className="timeline-item">
                    <div className="timeline-icon">
                      <svg viewBox="0 0 24 24">
                        <path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm-2 10h-4v4h-2v-4H7v-2h4V7h2v4h4v2z"></path>
                      </svg>
                    </div>
                    <div className="timeline-content">
                      <h3>{t("main.getDrawTime")}</h3>
                      <p>
                        {drawTime ? (
                          formatTimestamp(drawTime)
                        ) : (
                          <button 
                            className="text-btn" 
                            onClick={getDrawTime}
                            disabled={isLoading.drawTime}
                          >
                            {isLoading.drawTime ? (
                              <span className="spinner sm"></span>
                            ) : t("main.fetchData")}
                          </button>
                        )}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="card winner-card">
                <h2>{t("main.getLastWinner")}</h2>
                
                <div className="winner-content">
                  {lastWinner && lastWinner !== "0x0000000000000000000000000000000000000000" ? (
                    <div className="winner-address">
                      <svg className="winner-icon" viewBox="0 0 24 24">
                        <path d="M11.99 2C6.47 2 2 6.48 2 12s4.47 10 9.99 10C17.52 22 22 17.52 22 12S17.52 2 11.99 2zm4.24 16L12 15.45 7.77 18l1.12-4.81-3.73-3.23 4.92-.42L12 5l1.92 4.53 4.92.42-3.73 3.23L16.23 18z"></path>
                      </svg>
                      <p>{lastWinner}</p>

                      <a href={`https://etherscan.io/address/${lastWinner}#internaltx`} target="_blank" rel="noopener noreferrer">
                        {t("main.viewOnEtherscan")}
                      </a>
                    </div>
                  ) : (
                    <button 
                      className="winner-btn" 
                      onClick={getLastWinner}
                      disabled={isLoading.lastWinner}
                    >
                      {isLoading.lastWinner ? (
                        <span className="spinner"></span>
                      ) : (
                        <>
                          <svg className="refresh-icon" viewBox="0 0 24 24">
                            <path d="M17.65 6.35A7.958 7.958 0 0012 4c-4.42 0-7.99 3.58-7.99 8s3.57 8 7.99 8c3.73 0 6.84-2.55 7.73-6h-2.08A5.99 5.99 0 0112 18c-3.31 0-6-2.69-6-6s2.69-6 6-6c1.66 0 3.14.69 4.22 1.78L13 11h7V4l-2.35 2.35z"></path>
                          </svg>
                          {t("main.getLastWinner")}
                        </>
                      )}
                    </button>
                  )}
                </div>

                <div className="current-round">
                  <span className="round-label">{t("main.currentRound")}: </span>
                  <span className="round-value">
                    {currentRound !== null ? (
                      `#${currentRound.toString()}`
                    ) : (
                      <button 
                        className="text-btn" 
                        onClick={getCurrentRound}
                        disabled={isLoading.currentRound}
                      >
                        {isLoading.currentRound ? (
                          <span className="spinner sm"></span>
                        ) : t("main.fetchData")}
                      </button>
                    )}
                  </span>
                </div>
              </div>
            </section>

            <TicketHistory 
              account={account}
              myTickets={myTickets}
              historyFilter={historyFilter}
              setHistoryFilter={setHistoryFilter}
              formatDateString={formatDateString}
              formatIndex={formatIndex}
              viewTicketDetails={viewTicketDetails}
            />
          </>
        ) : (
          <ConnectPrompt connectWallet={connectWallet} />
        )}
      </main>
      
      <Footer countryCode={countryCode} account={account} />
    </div>
  );
}

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<MainApp />} />
        <Route path="/admin" element={<Admin />} />
      </Routes>
    </Router>
  );
}

export default App;
