import { EthereumProvider } from '@walletconnect/ethereum-provider';
import { getProjectId } from '../utils/GloabStringUtils';

// WalletConnect配置
const projectId = getProjectId();
const chains = [1]; // 仅允许Ethereum主网
const metadata = {
  name: 'YouToWin Lottery',
  description: 'Web3 Lottery Application',
  url: window.location.origin,
  icons: [`${window.location.origin}/ytw30.svg`]
};

class WalletConnectService {
  provider = null;
  connectPromise = null;
  connectResolve = null;
  connectReject = null;
  maxRetries = 3;
  retryCount = 0;
  
  // 创建一个Promise以便其他地方等待连接结果
  createConnectPromise() {
    this.connectPromise = new Promise((resolve, reject) => {
      this.connectResolve = resolve;
      this.connectReject = reject;
    });
    return this.connectPromise;
  }

  async init() {
    try {
      // 配置更稳定的连接选项
      this.provider = await EthereumProvider.init({
        projectId,
        chains,
        showQrModal: true,
        metadata,
        // 正确配置多个RPC端点
        rpcMap: {
          // 只保留一个用来创建Provider，其他的RPC通过JsonRpcProvider直接使用
          1: 'https://eth-mainnet.g.alchemy.com/v2/4bk26f-CWv3MH8c0UMYJRZHk2B7WQpli'
        },
        // 重要：启用后备基础设施提供更稳定的连接
        relayUrl: "wss://relay.walletconnect.org",
        // 扩展配置提高稳定性
        qrModalOptions: {
          desktopWallets: true,
          mobileWallets: true,
          explorerRecommendedWalletIds: undefined, // 使用默认推荐
          explorerExcludedWalletIds: "ALL"
        },
        // 请求超时设置更长以适应网络波动
        httpHeaders: {
          "User-Agent": "WalletConnect-YouToWin/1.0.0"
        },
        connectionTimeout: 20000, // 20秒
      });
      
      // 设置事件监听器
      this.setupEventListeners();
      
      return this.provider;
    } catch (error) {
      //console.error('Error initializing WalletConnect:', error);
      throw error;
    }
  }

  setupEventListeners() {
    if (!this.provider) return;

    this.provider.on('connect', (info) => {
      //console.log('WalletConnect connected!', info);
      if (this.connectResolve) {
        this.connectResolve(this.provider.accounts[0]);
      }
    });

    this.provider.on('disconnect', () => {
      //console.log('WalletConnect disconnected!');
    });

    this.provider.on('accountsChanged', (accounts) => {
      //console.log('WalletConnect accounts changed:', accounts);
    });

    this.provider.on('chainChanged', (chainId) => {
      //console.log('WalletConnect chain changed:', chainId);
    });

    this.provider.on('session_delete', () => {
      //console.log('WalletConnect session deleted');
    });

    // 添加错误处理
    this.provider.on('error', (error) => {
      //console.error('WalletConnect error:', error);
      if (this.connectReject) {
        this.connectReject(error);
      }
    });
    
    // 监听连接失败
    this.provider.on('display_uri', () => {
      //console.log('WalletConnect QR code displayed');
    });
  }

  async connect() {
    try {
      this.createConnectPromise();
      
      if (!this.provider) {
        await this.init();
      }
      
      // 如果已经连接，直接返回账户
      if (this.provider.connected) {
        return this.provider.accounts[0];
      }

      // 尝试连接
      try {
        // 创建带有超时的连接尝试
        const accounts = await Promise.race([
          this.provider.enable(),
          new Promise((_, reject) => 
            setTimeout(() => reject(new Error('Connection timed out')), 30000)
          )
        ]);
        
        // 重置重试计数
        this.retryCount = 0;
        return accounts[0]; // 返回第一个账户
      } catch (error) {
        // 连接中断或超时，尝试重连
        if (this.retryCount < this.maxRetries) {
          this.retryCount++;
          //console.log(`Connection attempt ${this.retryCount} failed, retrying...`);
          // 重置提供者并重试
          this.provider = null;
          return this.connect();
        }
        throw error;
      }
    } catch (error) {
      //console.error('Error connecting with WalletConnect:', error);
      if (this.connectReject) {
        this.connectReject(error);
      }
      throw error;
    }
  }

  async disconnect() {
    try {
      if (this.provider) {
        await this.provider.disconnect();
      }
      // 重置状态
      this.provider = null;
      this.retryCount = 0;
    } catch (error) {
      //console.error('Error disconnecting WalletConnect:', error);
      throw error;
    }
  }

  getProvider() {
    return this.provider;
  }
}

// 导出单例实例
export default new WalletConnectService(); 