import React from 'react';
import { useTranslation } from 'react-i18next';

const Header = ({ 
  account, 
  isConnecting, 
  networkError, 
  connectWallet, 
  disconnectWallet, 
  countryCode, 
  formatAddress 
}) => {
  const { t } = useTranslation();

  return (
    <header className="app-header">
      <div className="logo">
        <img src="./ytw30.svg" alt="logo" className="logo-icon" />
        <h1>{t("main.title")}</h1>
      </div>
      
      <div className="wallet-connect">
        {!account ? (
          <button className="connect-btn" onClick={connectWallet} disabled={isConnecting}>
            {isConnecting ? (
              <>
                <span className="spinner"></span>
                {t("main.connecting")}
              </>
            ) : (
              <>
                <svg className="wallet-icon" viewBox="0 0 24 24">
                  <path d="M19 7h-1V6a3 3 0 00-3-3H5a3 3 0 00-3 3v12a3 3 0 003 3h14a3 3 0 003-3v-8a3 3 0 00-3-3zm-8 3a2 2 0 11-2 2 2 2 0 012-2z"></path>
                </svg>
                {t("main.connectWallet")}
              </>
            )}
          </button>
        ) : (
          <div className="account-info">
            <div className="account-badge">
              <svg className="wallet-icon-sm" viewBox="0 0 24 24">
                <path d="M19 7h-1V6a3 3 0 00-3-3H5a3 3 0 00-3 3v12a3 3 0 003 3h14a3 3 0 003-3v-8a3 3 0 00-3-3zm-8 3a2 2 0 11-2 2 2 2 0 012-2z"></path>
              </svg>
              <span className="address">{formatAddress(account)}</span>
            </div>
            {countryCode && (
              <div className="country-badge">
                <span>{countryCode}</span>
              </div>
            )}
            <button className="disconnect-btn" onClick={disconnectWallet}>
              <svg className="disconnect-icon" viewBox="0 0 24 24">
                <path d="M17 7l-1.41 1.41L18.17 11H8v2h10.17l-2.58 2.58L17 17l5-5zM4 5h8V3H4c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h8v-2H4V5z"></path>
              </svg>
            </button>
          </div>
        )}
        {account && networkError && (
          <div className="network-error-banner">
            <svg className="warning-icon" viewBox="0 0 24 24">
              <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-2h2v2zm0-4h-2V7h2v6z"></path>
            </svg>
            <span>
              {t("main.onlyMainnet", {
                current: `Chain ID: ${networkError.current}`
              })}
            </span>
          </div>
        )}
      </div>
    </header>
  );
};

export default Header; 