import React from 'react';
import { useTranslation } from 'react-i18next';

const TicketHistory = ({
  account,
  myTickets,
  historyFilter,
  setHistoryFilter,
  formatDateString,
  formatIndex,
  viewTicketDetails
}) => {
  const { t } = useTranslation();
  
  // 处理过滤后的历史记录
  const filteredHistory = () => {
    if (!myTickets || myTickets.length === 0) return [];
    if (historyFilter === 'all') return myTickets;
    return myTickets.filter(item => item.status === historyFilter);
  };
  
  // 渲染票据表格
  const renderTicketTable = () => {
    const filtered = filteredHistory();
    
    if (filtered.length === 0) {
      return (
        <div className="empty-state">
          <svg className="empty-icon" viewBox="0 0 24 24">
            <path d="M13 2.05v3.03c3.39.49 6 3.39 6 6.92 0 .9-.18 1.75-.48 2.54l2.6 1.53c.56-1.24.88-2.62.88-4.07 0-5.18-3.95-9.45-9-9.95zM12 19c-3.87 0-7-3.13-7-7 0-3.53 2.61-6.43 6-6.92V2.05c-5.06.5-9 4.76-9 9.95 0 5.52 4.47 10 9.99 10 3.31 0 6.24-1.61 8.06-4.09l-2.6-1.53A7.95 7.95 0 0 1 12 19z"/>
          </svg>
          <p>{t("ticketHistory.noTickets")}</p>
        </div>
      );
    }
    
    return (
      <div className="ticket-table-container">
        <table className="ticket-table">
          <thead>
            <tr>
              <th>{t("ticketHistory.table.round")}</th>
              <th>{t("ticketHistory.table.tickets")}</th>
              <th>{t("ticketHistory.table.status")}</th>
              <th>{t("ticketHistory.table.prize")}</th>
              <th>{t("ticketHistory.table.date")}</th>
              <th>{t("ticketHistory.table.actions")}</th>
            </tr>
          </thead>
          <tbody>
            {filtered.map((record, index) => (
              <tr key={`round-${record.round}-${index}`}>
                <td title={`#${record.round}`}>
                  <span className="round-number">#{record.round}</span>
                </td>
                <td>
                  <div className="tickets-display">
                    <div className="tickets-header">
                      <svg className="ticket-icon" viewBox="0 0 24 24">
                        <path d="M22 10V6a2 2 0 0 0-2-2H4a2 2 0 0 0-2 2v4a2 2 0 1 1 0 4v4a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2v-4a2 2 0 1 1 0-4zm-10 7a3 3 0 1 1 0-6 3 3 0 0 1 0 6z"/>
                      </svg>
                      <span className="ticket-count">{record.tickets.length}</span>
                    </div>
                    <div className="ticket-numbers">
                      {formatIndex(record.tickets)}
                    </div>
                  </div>
                </td>
                
                <td>
                  <span 
                    className={`status-tag ${record.status}`}
                    title={record.status === 'won' 
                      ? t("ticketHistory.status.won") 
                      : record.status === 'pending' 
                        ? t("ticketHistory.status.pending") 
                        : t("ticketHistory.status.notWon")}
                  >
                    {record.status === 'won' 
                      ? t("ticketHistory.status.won") 
                      : record.status === 'pending' 
                        ? t("ticketHistory.status.pending") 
                        : t("ticketHistory.status.notWon")}
                  </span>
                </td>
                <td title={record.prize ? `${record.prize} ETH` : "-"}>
                  {record.prize ? (
                    <div className="prize-cell">
                      <span className="eth-icon">Ξ</span>
                      <span>{record.prize}</span>
                    </div>
                  ) : (
                    <span>-</span>
                  )}
                </td>
                <td title={formatDateString(record.date)}>
                  <div className="date-cell">
                    {formatDateString(record.date)}
                  </div>
                </td>
                <td>
                  <div className="action-buttons">
                    <button 
                      className="history-action-btn view-address" 
                      title={t("ticketHistory.viewByAddress")}
                      onClick={() => window.open(`https://etherscan.io/address/${account}`, '_blank')}
                    >
                      <svg className="action-icon" viewBox="0 0 24 24">
                        <path d="M12 4.5C7 4.5 2.73 7.61 1 12c1.73 4.39 6 7.5 11 7.5s9.27-3.11 11-7.5c-1.73-4.39-6-7.5-11-7.5zM12 17c-2.76 0-5-2.24-5-5s2.24-5 5-5 5 2.24 5 5-2.24 5-5 5zm0-8c-1.66 0-3 1.34-3 3s1.34 3 3 3 3-1.34 3-3-1.34-3-3-3z"/>
                      </svg>
                    </button>
                    
                    {record.txHash && (
                      <button 
                        className="history-action-btn view-etherscan" 
                        title={t("ticketHistory.viewOnEtherscan")}
                        onClick={() => viewTicketDetails()}
                      >
                        <svg className="action-icon" viewBox="0 0 24 24">
                          <path d="M19 19H5V5h7V3H5a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7h-2v7zM14 3v2h3.59l-9.83 9.83 1.41 1.41L19 6.41V10h2V3h-7z"/>
                        </svg>
                      </button>
                    )}
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  return (
    <section className="ticket-history-section">
      <div className="section-header">
        <h2>{t("ticketHistory.title")}</h2>
        <div className="filter-group">
          <button 
            className={`filter-btn ${historyFilter === 'all' ? 'active' : ''}`} 
            onClick={() => setHistoryFilter('all')}
          >
            {t("ticketHistory.filter.all")}
          </button>
          <button 
            className={`filter-btn ${historyFilter === 'won' ? 'active' : ''}`} 
            onClick={() => setHistoryFilter('won')}
          >
            {t("ticketHistory.filter.won")}
          </button>
          <button 
            className={`filter-btn ${historyFilter === 'pending' ? 'active' : ''}`} 
            onClick={() => setHistoryFilter('pending')}
          >
            {t("ticketHistory.filter.pending")}
          </button>
        </div>
      </div>
      
      {renderTicketTable()}
    </section>
  );
};

export default TicketHistory; 